@use "../../settings/variables" as *;

$mark-border-color: #4444441a;
$mark-border-color-checked: $primary-theme-color;

.control-button {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  height: 52px;
  cursor: pointer;
}

.control-button__input {
  &:checked + .control-button__mark {
    border-color: $mark-border-color-checked;
  }
}

.control-button__mark {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border: 2px solid $mark-border-color;
  transition: border-color $transition-duration ease;

  &:hover {
    border-color: #4444444d;
  }
}

.control-button__label {
  font-weight: 700;
  font-size: $font-size-modal-buttons;
  line-height: $line-height-modal-buttons;
  color: $primary-font-color;
}
