@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Open Sans", sans-serif;
}

.container {
  width: 100%;
  max-width: 1150px;
  padding: 0 30px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    padding: 0 50px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px !important;
  /* stylelint-disable-line declaration-no-important */
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.section {
  padding: 31px 0 42px;
}
.section--bordered {
  background: url("../img/components/zigzag-line.svg") no-repeat bottom;
  background-size: 100% auto;
}
@media (min-width: 1150px) {
  .section--bordered {
    background-size: 1050px auto;
    background-position: center bottom;
  }
}

@media (min-width: 768px) {
  .section__container {
    display: grid;
    grid-template-columns: 1fr 50%;
    grid-template-rows: auto 1fr auto auto;
    column-gap: 42px;
  }
}
@media (min-width: 1150px) {
  .section__container {
    grid-template-columns: 117px 1fr 50%;
    grid-template-rows: 118px 1fr auto auto;
    column-gap: 58px;
  }
}

.section__header {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .section__header {
    flex-direction: row;
    justify-content: space-between;
  }
}
@media (min-width: 1150px) {
  .section__header {
    display: grid;
    grid-template-columns: 117px 1fr;
    column-gap: 58px;
    grid-auto-flow: dense;
    align-items: end;
  }
}

.section__title {
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  color: #000000;
  margin: 0;
}
@media (min-width: 768px) {
  .section__title {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .section__title {
    grid-column: 2;
    font-size: 53px;
    line-height: 57px;
  }
}

.section__subtitle {
  margin: 0;
  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
  color: #63d1bb;
  text-transform: uppercase;
}
@media (min-width: 1150px) {
  .section__subtitle {
    grid-column: 1;
  }
}

.button {
  display: inline-block;
  padding: 14px;
  margin: 0;
  min-width: 190px;
  text-decoration: none;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  text-transform: uppercase;
  text-align: center;
  cursor: pointer;
  transition: background-color 0.15s ease, color 0.15s ease;
}
.button--primary {
  background: none;
  border: 2px solid #444444;
  color: #444444;
}
.button--add {
  background-color: #63d1bb;
  border: none;
  color: #ffffff;
  font-size: 17px;
  line-height: 16px;
  transition: opacity 0.15s ease;
  padding-top: 20px;
}
.button--add:hover {
  opacity: 0.6;
}
.button--add:active {
  opacity: 0.3;
}
.button--primary:not(.button--disabled):hover {
  background-color: #444444;
  color: #ffffff;
}
.button--primary:not(.button--disabled):focus-visible {
  outline-color: #63d1bb;
  background-color: #444444;
  color: #ffffff;
}
@supports not selector(:focus-visible) {
  .button--primary:not(.button--disabled):focus {
    outline-color: #63d1bb;
    background-color: #444444;
    color: #ffffff;
  }
}
.button--primary:not(.button--disabled):active {
  color: rgba(255, 255, 255, 0.3);
}
.button:disabled, .button--disabled {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .button {
    font-size: 17px;
  }
}

@media (min-width: 768px) {
  .slider {
    display: grid;
    grid-template-columns: 1fr auto;
    grid-template-rows: repeat(2, auto);
  }
}

.slider__list {
  list-style: none;
  padding: 0;
  margin: 0;
}
@media (min-width: 768px) {
  .slider__list {
    grid-column: 1/-1;
    grid-row: 1/-1;
  }
}

.slider__item {
  display: none;
  padding: 15px 0 33px;
}
.slider__item--active {
  display: block;
}
@media (min-width: 768px) {
  .slider__item {
    padding: 40px 0 0;
  }
}

.slider__buttons {
  display: flex;
}
@media (min-width: 768px) {
  .slider__buttons {
    grid-column: 2;
    grid-row: 2;
    max-width: 175px;
    position: relative;
    top: -5px;
  }
}
@media (min-width: 1150px) {
  .slider__buttons {
    top: 0;
  }
}

.slider__arrow {
  width: 130px;
  padding: 0;
  background: none;
  border: 1px solid #ececec;
  height: 52px;
  position: relative;
  cursor: pointer;
  transition: opacity 0.15s ease;
}
.slider__arrow--right {
  border-left: none;
}
.slider__arrow::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  mask: url("../img/components/arrow.svg") no-repeat center;
  background-color: #000000;
}
.slider__arrow--right::after {
  transform: scaleX(-1);
}
.slider__arrow:hover {
  opacity: 0.6;
}
.slider__arrow:focus {
  outline-color: #63d1bb;
}
.slider__arrow:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .slider__arrow {
    border-color: transparent;
  }
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 50;
  display: none;
  align-items: center;
  justify-content: center;
  background-color: #ffffffe5;
}
.modal--active {
  display: flex;
}

.modal__inner {
  width: 100%;
  max-width: 566px;
  padding: 29px 28px 46px;
  background-color: #ffffff;
  border: 1px solid #c6c6c6;
  box-shadow: 0 5px 10px #0001013f;
}
@media (min-width: 768px) {
  .modal__inner {
    padding-top: 35px;
    padding-bottom: 50px;
  }
}

.control-button {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  height: 52px;
  cursor: pointer;
}

.control-button__input:checked + .control-button__mark {
  border-color: #63d1bb;
}

.control-button__mark {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border: 2px solid #4444441a;
  transition: border-color 0.15s ease;
}
.control-button__mark:hover {
  border-color: #4444444d;
}

.control-button__label {
  font-weight: 700;
  font-size: 17px;
  line-height: 16px;
  color: #444444;
}

.header {
  border-top: 1px solid #ececec;
  border-bottom: 1px solid #ececec;
}
@media (max-width: 767px) {
  .header--nojs .header__logo {
    position: static;
    transform: translate(0, 0);
    padding: 1em 0;
  }
  .header--nojs .nav {
    display: flex;
    width: 100%;
    position: static;
  }
  .header--nojs .burger {
    display: none;
  }
}

.header__container {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-end;
  min-height: 78px;
}
@media (min-width: 768px) {
  .header__container {
    min-height: 86px;
  }
}

.header__logo {
  margin: 0 auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 15;
  order: 1;
  transition: opacity 0.15s ease;
}
.header__logo[href]:hover {
  opacity: 0.6;
}
.header__logo:focus {
  outline-color: #63d1bb;
}
.header__logo[href]:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .header__logo {
    position: relative;
    top: 2px;
    left: 0;
    transform: translate(0, 0);
    margin-left: 0;
  }
}
@media (min-width: 1150px) {
  .header__logo {
    margin-left: auto;
  }
}

.header__burger {
  order: 2;
}

.header__nav {
  position: absolute;
  top: calc(100% + 1px);
  left: 0;
  right: 0;
  order: 3;
}
@media (min-width: 768px) {
  .header__nav {
    top: 0;
    padding: 0 20px;
  }
}

.header__text {
  display: none;
  position: absolute;
  right: 30px;
  bottom: -46px;
  margin: 0;
  font-size: 15px;
  line-height: 24px;
  color: #444444;
}
@media (min-width: 768px) {
  .header__text {
    display: block;
  }
}

.nav {
  display: none;
  flex-direction: column;
  z-index: 10;
  background-color: #ffffff;
}
.nav--active {
  display: flex;
}
@media (min-width: 768px) {
  .nav {
    display: flex;
    background: none;
  }
}
@media (min-width: 1150px) {
  .nav {
    flex-direction: row;
  }
}

/*=== Lists ===*/
.nav__list,
.nav__user-list {
  display: flex;
  flex-direction: column;
  list-style: none;
  flex-wrap: wrap;
  padding: 0;
  margin: 0;
}
@media (min-width: 768px) {
  .nav__list,
.nav__user-list {
    flex-direction: row;
  }
}
@media (min-width: 1150px) {
  .nav__list,
.nav__user-list {
    order: 0;
    max-width: 500px;
    align-content: start;
  }
}

@media (min-width: 768px) {
  .nav__list {
    order: 2;
    padding-right: 285px;
  }
}
@media (min-width: 1150px) {
  .nav__list {
    order: 0;
    padding-right: 0;
  }
}

@media (min-width: 768px) {
  .nav__user-list {
    position: relative;
    align-self: flex-start;
    margin-left: auto;
    padding-left: 150px;
  }
}
@media (min-width: 1150px) {
  .nav__user-list {
    padding-left: 0;
  }
}

/*=== Items ===*/
.nav__item,
.nav__user-item {
  position: relative;
  font-size: 17px;
  font-weight: 700;
  line-height: 24px;
}
.nav__item--desktop-only,
.nav__user-item--desktop-only {
  font-size: 15px;
  display: none;
}
@media (min-width: 1150px) {
  .nav__item--desktop-only,
.nav__user-item--desktop-only {
    display: block;
  }
}

@media (min-width: 1150px) {
  .nav__item:first-child::after {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    width: 1px;
    background-color: #ececec;
  }
}

@media (min-width: 768px) {
  .nav__user-item--search .nav__user-link {
    padding-left: 41px;
    padding-right: 40px;
  }
}
.nav__user-item--search::after, .nav__user-item--cart::after {
  content: "";
  position: absolute;
  top: 48%;
  left: 30px;
  height: 18px;
  width: 21px;
  transform: translateY(-50%);
  background-color: rgba(0, 0, 0, 0.2);
  z-index: -1;
  transition: background-color 0.15s ease;
}
@media (min-width: 768px) {
  .nav__user-item--search::after, .nav__user-item--cart::after {
    top: 50%;
    left: 32px;
  }
}
.nav__user-item--search::after {
  mask: url("../img/menu/menu-search.svg") no-repeat center;
}
.nav__user-item--cart::after {
  mask: url("../img/menu/menu-cart.svg") no-repeat center;
}
.nav__user-item:hover::after {
  background-color: #63d1bb;
}
.nav__user-item:active::after {
  background-color: rgba(99, 209, 187, 0.2);
}

/*=== Links ===*/
.nav__link,
.nav__user-link {
  display: block;
  text-decoration: none;
  color: #000000;
  padding: 27px 30px 27px 83px;
  height: 100%;
  text-align: left;
  vertical-align: middle;
  border-bottom: 1px solid #ececec;
  transition: opacity 0.15s ease;
}
.nav__link[href]:hover,
.nav__user-link[href]:hover {
  opacity: 0.6;
}
.nav__link:focus,
.nav__user-link:focus {
  outline-color: #63d1bb;
}
.nav__link[href]:active,
.nav__user-link[href]:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .nav__link,
.nav__user-link {
    border: none;
    padding-top: 31px;
    padding-bottom: 31px;
  }
}

@media (min-width: 768px) {
  .nav__link {
    font-size: 15px;
    padding: 22px 7px 21px 30px;
  }
}
@media (min-width: 1150px) {
  .nav__link {
    padding: 32px 36px 31px 32px;
    font-size: 17px;
  }
  .nav__link--desktop-only {
    font-size: 15px;
    padding: 21px 31px;
  }
}

@media (min-width: 768px) {
  .nav__user-link--bordered {
    border-left: 1px solid #ececec;
  }
}

@media (min-width: 768px) {
  .nav__user-link-span {
    display: none;
  }
}

.burger {
  background: none;
  border: none;
  padding: 0;
  position: relative;
  align-self: center;
  height: 14px;
  width: 20px;
  cursor: pointer;
  transition: opacity 0.15s ease;
}
.burger:hover {
  opacity: 0.6;
}
.burger:focus {
  outline-color: #63d1bb;
}
.burger:active {
  opacity: 0.3;
}
.burger::before, .burger::after {
  content: "";
  position: absolute;
  height: 2px;
  background-color: #231f20;
  left: 0;
  right: 0;
  transition: transform 0.4s ease;
}
.burger::before {
  top: 0;
}
.burger::after {
  bottom: 0;
}
.burger--active .burger__item {
  opacity: 0;
}
.burger--active::before {
  transform: translateY(6px) rotate(-45deg);
}
.burger--active::after {
  transform: translateY(-6px) rotate(45deg);
}
@media (min-width: 768px) {
  .burger {
    display: none;
  }
}

.burger__item {
  display: block;
  width: 100%;
  height: 2px;
  background-color: #231f20;
  transition: opacity 0.4s ease;
}

.hero {
  background-color: #f1f1f1;
  min-height: 177px;
  display: flex;
  flex-direction: column;
}
.hero--index {
  background-image: url("../img/index/hero/background/hero-mobile.jpg");
  background-color: #f9f9f9;
  background-repeat: no-repeat;
  background-position: center -19px;
  min-height: 488px;
  padding: 71px 0 0 0;
}
@media (min-resolution: 2dppx) {
  .hero--index {
    background-image: url("../img/index/hero/background/hero-mobile@2x.jpg");
    background-size: 320px auto;
  }
}
.hero--simple {
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .hero {
    min-height: 226px;
    position: relative;
  }
  .hero::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 24px;
    bottom: -12px;
    background: url("../img/components/zigzag-fill-desktop.svg") center bottom;
  }
  .hero--index {
    min-height: 746px;
    padding-top: 155px;
    background-size: auto 100%;
    background-position: center top;
    background-image: url("../img/index/hero/background/hero-desktop.jpg");
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .hero--index {
    background-image: url("../img/index/hero/background/hero-desktop@2x.jpg");
  }
}
@media (min-width: 768px) {
  .hero--simple {
    padding-top: 26px;
  }
}
@media (min-width: 1150px) {
  .hero--index {
    padding-top: 159px;
  }
  .hero::after {
    background-repeat: repeat-x;
    width: 100%;
    height: 40px;
    background-size: contain;
    bottom: -21px;
  }
  .hero--simple {
    padding-top: 4px;
    min-height: 256px;
  }
  .hero--simple::after {
    bottom: -17px;
  }
}

.hero__container {
  margin-bottom: auto;
}

.hero__title {
  text-align: center;
  font-size: 27px;
}
.hero__title--index {
  margin: 0 0 120px 0;
}
@media (min-width: 768px) {
  .hero__title {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .hero__title {
    font-size: 53px;
    line-height: 57px;
  }
}

.hero__buttons {
  display: flex;
  flex-direction: column;
}
@media (max-width: 767px) {
  .hero__buttons {
    padding: 0;
  }
}
@media (min-width: 768px) {
  .hero__buttons {
    flex-direction: row;
  }
}

.product-toggle {
  font-family: inherit;
  display: block;
  width: 100%;
  border: none;
  padding: 24px 0 32px;
  text-align: left;
  color: #ffffff;
  font-weight: 700;
  font-size: 19px;
  line-height: 24px;
  position: relative;
  cursor: pointer;
  transition: background-color 0.15s ease, transform 0.15s ease;
}
@media (min-width: 768px) {
  .product-toggle {
    padding-top: 26px;
    font-size: 20px;
    line-height: 30px;
  }
}
.product-toggle::before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: -1;
  bottom: 0;
  background-color: inherit;
  transition: transform 0.15s ease;
}
.product-toggle--interior {
  background-color: #63d1bb;
}
.product-toggle--toys {
  background-color: #56c3b2;
}
.product-toggle--interior::after, .product-toggle--toys::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  display: block;
  background-color: #149080;
  opacity: 0.5;
  transition: opacity 0.15s ease;
}
.product-toggle--interior::after {
  mask: url("../img/index/hero/interior.svg") no-repeat center;
  width: 40px;
  right: 35px;
  top: 4px;
}
.product-toggle--toys::after {
  mask: url("../img/index/hero/toy.svg") no-repeat center;
  width: 60px;
  right: 28px;
}
@media (min-width: 768px) {
  .product-toggle--toys::after {
    right: 33px;
  }
}
.product-toggle--interior:hover {
  background-color: #59d5bc;
}
.product-toggle:hover {
  transform: translateY(-5px);
}
.product-toggle:hover::before {
  transform: translateY(5px);
}
.product-toggle--toys:hover {
  background-color: #46c1ae;
}
.product-toggle:hover::after {
  opacity: 0.7;
}
.product-toggle:active::after {
  opacity: 0.3;
}

.product-toggle__container {
  display: block;
  width: 150px;
  margin: 0 auto 0 0;
}
@media (min-width: 768px) {
  .product-toggle__container {
    padding-left: 35px;
  }
}

@media (min-width: 768px) {
  .popular-product {
    padding: 52px 0 68px;
  }
}
@media (min-width: 1150px) {
  .popular-product {
    padding: 71px 0 95px;
  }
}

.popular-product__header {
  margin-bottom: 17px;
}
@media (min-width: 768px) {
  .popular-product__header {
    grid-row: 1;
    grid-column: 1/-1;
    margin-bottom: 38px;
  }
}
@media (min-width: 1150px) {
  .popular-product__header {
    margin-bottom: 0;
  }
}

.popular-product__title {
  margin-bottom: 4px;
  position: relative;
  z-index: 1;
}
@media (min-width: 768px) {
  .popular-product__title {
    margin: 0;
  }
}

@media (min-width: 768px) {
  .popular-product__subtitle {
    line-height: 1;
    align-self: center;
    width: 126px;
    text-align: center;
  }
}
@media (min-width: 1150px) {
  .popular-product__subtitle {
    margin: 0 -15px 15px;
    justify-self: end;
    align-self: end;
    width: 100px;
  }
}

.popular-product__description {
  margin: 0 0 18px;
  font-size: 15px;
  line-height: 24px;
  color: #444444;
}
@media (min-width: 768px) {
  .popular-product__description {
    font-size: 17px;
    line-height: 30px;
    margin-bottom: 38px;
  }
}
@media (min-width: 1150px) {
  .popular-product__description {
    margin-bottom: 54px;
  }
}

.popular-product__image-wrapper {
  margin: 28px 0 25px;
}
@media (min-width: 768px) {
  .popular-product__image-wrapper {
    grid-column: 2;
    grid-row: 2/-1;
    margin: 12px 0 0;
  }
}
@media (min-width: 1150px) {
  .popular-product__image-wrapper {
    grid-column: 3;
    grid-row: 1/-1;
    margin: 0;
  }
}

.popular-product__image {
  display: block;
  width: 100%;
  height: auto;
  object-fit: contain;
  object-position: center;
